import './styles/style.less';
import HomePage from '@/components/pages/index.vue';
import Page from '@/components/pages/Pages1.vue';
import Page2 from '@/components/pages/Pages2.vue';
import Page3 from '@/components/pages/Pages3.vue';
import Page4 from '@/components/pages/Pages4.vue';
import Page5 from '@/components/pages/Pages5.vue';
import Page6 from '@/components/pages/Pages6.vue';
import Page7 from '@/components/pages/Pages7.vue';
import 'animate.css';
export default {
  name: 'App',
  components: {
    Page,
    Page2,
    Page3,
    Page4,
    Page5,
    Page6,
    Page7,
    HomePage
  },
  data() {
    return {
      pageArray: ['homePage', 'page1', 'page2', 'page3', 'page4', 'page5', 'page6', 'page7'],
      showPage1: false,
      showPage2: false,
      currentPage: '',
      currentPageIndex: 0,
      loading: true
    };
  },
  async beforeCreate() {
    // 预加载占用资源较大的图片
    let imagesArray = [require('@/assets/person1.png'), require('@/assets/person1_cloud.png'), require('@/assets/person2.png'), require('@/assets/person2_cloud.png'), require('@/assets/new_year.png'), require('@/assets/fireworks.gif'), require('@/assets/tuzi.png'), require('@/assets/test.gif')];
    const promise = imagesArray.map(i => {
      return new Promise(resolve => {
        console.log(i);
        const image = new Image();
        image.src = i;
        image.onload = () => {
          resolve();
          console.log(i);
          console.log('图片加载成');
        };
      });
    });
    await Promise.all(promise);
    this.loading = false;
    // imagesArray.forEach(item => {
    //   const image = new Image()
    //   image.src = item
    //   image.onload = () => {
    //     console.log('图片加载成功')
    //   }
    // })
  },

  created() {
    this.currentPage = this.pageArray[0];
  },
  methods: {
    onPlayVideo() {
      const video = document.getElementById('video');
      video.play();
    },
    onNextpage() {
      const pageArrayLength = this.pageArray.length;
      if (this.currentPageIndex + 1 < pageArrayLength) {
        this.currentPage = this.pageArray[this.currentPageIndex + 1];
        this.currentPageIndex++;
      }
      console.log(this.currentPage);
      console.log(this.currentPageIndex);
    }
  }
};